body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
  width: 32%;
  margin: 0 auto;
  padding: 0.5rem;
  border: 3px outset;
  border-radius: 0.4rem;
}

.loading-message {
  padding-top: 5rem;
  text-align: center;
  font-weight: 600;
  font-size: 2rem;
  color: #458E8B;
}

.loading-error-message {
  color: #dc3545;
}

button:disabled {
  cursor: not-allowed;
}

.btn-action {
  width:30%;
  min-width:25%;
}

.amount-box {
  font-size: 1.25rem;
}
